html,
body {
  margin: 0%;
  font-family: 'Nunito';
  color: var(--text-color);
  font-size: 1.25rem;
  background-color: var(--bg);
  height: 100%;
}

:root {
  --bg: #444444;
  --bg-dark: #292929;
  --text-color: #8E8E8E;
  --sidebar-size: calc(15% + 2rem);
  --border: 1px solid #444444;
  --border-radius: 8px;
}

button {
  background-color: var(--bg);
  color: var(--text-color);
  border: none;
  outline: none;
  padding: 8px;
  font-size: 1.2rem;
  cursor: pointer;
}

.sidebar {
  margin: 0;
  padding: 0;
  width: var(--sidebar-size);
  height: 100%;
  background-color: var(--bg-dark);
  overflow: hidden;
  position: fixed;
}

.sidebar-back {
  padding: 1rem;
  font-size: 1.5rem;
}

.sidebar-content {
  height: calc(100% - 82px - 81px - 40px);
  padding: 1rem;
}

.sidebar-header {
  font-size: 1.5rem;
  border-bottom: var(--border);
  display: flex;
  align-items: center;
  padding: 1rem;
  position: sticky;
}

.sidebar-item {
  display: block;
  margin-bottom: 10px;
}

.sidebar-links {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  overflow: hidden;
  position: sticky;
  font-size: 1.5rem;
}

.tab {
  position: relative;
  overflow: auto;
  padding: 1rem 0;
  margin-left: calc(var(--sidebar-size) + 1rem);
  margin-right: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.container {
  background-color: var(--bg-dark);
  border: var(--border);
  border-radius: var(--border-radius);
  margin-bottom: 10px;
  overflow: hidden;
}

.container button{
  width: 14%;
  margin: 0 10px;
}

.container select {
  width: 30%;
  padding: 6px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.container-input {
  float: right;
  clear: both;
  width: 30%;
  padding: 6px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  height: 24.3px;
}

[type="checkbox"] {
  margin: 10px 0;
}

.container-input-time {
  float: right;
  padding: 6px;
  height: 34px;
}

.right {
  float: right;
}

.container-header, .container-footer {
  border-bottom: var(--border);
  padding: 1rem;
  min-height: 1.75rem;
}

.container-content {
  padding: 1rem;
}

.color-1{
  color: #0c9200
}

.color-2{
  color: #ee5700
}

.color-4 {
  color: #ff5252
}

.color-0{
  color: var(--text-color)
}

.width-50 {
  flex: 1; /* Two in one row */
}

.width-100 {
  width: 100%;
}

.width-33 {
  flex: 2; /* Three in one row */
}

.width-auto {
  width: auto;
}

.login {
  background-color: var(--bg-dark);
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  max-height: 350px;
  max-width: 350px;
  padding: 162px 75px;
}

.loginForm{
  color: var(--text-color);
}

.loginForm input {
  width: 100%;
  padding: 6px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.loginFormButton button{
  margin-top: 30px;
  width: 100%;
}

.input button {
  width: 15%;
  padding: 10.85px;
  float: right;
  height: 50px;
  margin-bottom: 7px;
}

.input input[type=text], .input input[type="date"], .input input[type='time'], .input input[type=number], .input input[type=datetime-local], .input input[type=password], .input select {
  width: 100%;
  padding: 6px 10px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.container table {
  width: 100%;
  table-layout: fixed;
}

.container table button {
  width: 100%;
  font-size: 1.7rem;
  padding: 0;
  margin: 0;
}

.container table svg {
  margin-top: 5px;
}

.container table tr {
  height: 46px;
}

.aufgabe {
  display: flex;
  text-align: center;
}

.aufgabe-content {
  width: 150px;
  border: 1px solid var(--bg-dark);
  color: var(--text-color);
  background-color: var(--bg);
  margin: auto;
  padding: 5px;
}

.tableborder + .tableborder {
  border-left: 2px solid var(--bg);
}